import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';
import Url from 'url';

import { HeaderPage, Layout, ImgSharp } from '../components/common';

import { PostClass } from '../components/common/helpers';
import { WebsiteMeta } from '../components/common/meta';

/**
 * Single page (/:slug)
 *
 * This file renders a single page and loads all the content.
 *
 */
const Page = ({ children, location, pageTitle, featureImage }) => {
  const {
    site: {
      siteMetadata: { siteUrl },
    },
  } = useStaticQuery(graphql`
    query SiteMetaDataQueryPage {
      site {
        siteMetadata {
          siteUrl
        }
      }
    }
  `);

  const canonical = Url.resolve(siteUrl, location.pathname);

  const postClass = PostClass({
    isPage: true,
    isImage: !!featureImage,
  });

  return (
    <>
      <WebsiteMeta title={pageTitle} canonical={canonical} type="WebSite" />
      <Layout isPage header={<HeaderPage />}>
        <div className="inner">
          <article className={`post-full ${postClass}`}>
            <header className="post-full-header">
              <h1 className="post-full-title">{pageTitle}</h1>
            </header>

            {featureImage && (
              <figure className="post-full-image">
                <ImgSharp
                  // fluidClass="kg-card kg-code-card"
                  // fluidImg={fluidFeatureImg}
                  // TODO: Remove below
                  srcClass="kg-card kg-code-card"
                  srcImg={featureImage}
                  title={pageTitle}
                />
              </figure>
            )}

            {/* The main page content */}
            <section className="post-full-content">
              <div className="post-content load-external-scripts">
                {children}
              </div>
            </section>
          </article>
        </div>
      </Layout>
    </>
  );
};

Page.propTypes = {
  children: PropTypes.node.isRequired,
  pageTitle: PropTypes.string.isRequired,
  featureImage: PropTypes.string,
  location: PropTypes.object.isRequired,
};

export default Page;
